.b-splited--contacts .b-splited__block {
  overflow: hidden; }

.contacts {
  font-family: "Circe";
  font-weight: 300;
  font-size: 20px;
  color: black;
  line-height: 160%;
  max-width: 276px;
  letter-spacing: 0.3px; }
  @media screen and (max-width: 768px) {
    .contacts {
      font-size: 16px; } }
  .contacts__address {
    font-style: normal;
    margin-bottom: 32px; }
    @media screen and (max-width: 768px) {
      .contacts__address {
        margin-bottom: 15px; } }
  .contacts__phone {
    display: block;
    color: black;
    margin-bottom: 32px; }
    @media screen and (max-width: 768px) {
      .contacts__phone {
        margin-bottom: 15px; } }
  .contacts__mail {
    display: block;
    color: black;
    text-decoration: underline; }

.slider-contacts {
  margin-top: 57px;
  max-width: 250px; }
  @media screen and (max-width: 1300px) {
    .slider-contacts {
      margin-top: 20px; } }
  .slider-contacts .item {
    font-family: "Circe"; }
  .slider-contacts .el {
    font-size: 16px;
    line-height: 160%; }
    .slider-contacts .el__image {
      margin-bottom: 23px; }
      .slider-contacts .el__image img {
        margin: auto;
        max-height: 128px;
        max-width: 128px; }
    .slider-contacts .el__title {
      font-weight: 700; }
    .slider-contacts .el__desc {
      font-weight: 300; }
  .slider-contacts .arrow {
    position: absolute;
    top: 45px; }
    .slider-contacts .arrow--prev {
      left: 0; }
    .slider-contacts .arrow--next {
      right: 0; }

.contacts-call {
  max-width: 140px;
  width: 100%;
  margin-top: 47px; }

.contacts-imedia {
  position: absolute;
  left: calc(50vw - 50px);
  transform: translateX(-100%);
  bottom: 40px;
  font-family: "Circe";
  display: flex;
  align-items: center;
  color: black;
  font-size: 12px; }
  @media screen and (max-width: 768px) {
    .contacts-imedia {
      position: static;
      transform: none;
      justify-content: center;
      margin: 20px auto 70px; } }
  .contacts-imedia span {
    margin-right: 10px; }
  .contacts-imedia:hover {
    text-decoration: underline; }
